@font-face {
    font-family: "Baskerville";
    src: url('fonts/BASKVILL.TTF') format("truetype");
}
@font-face {
    font-family: "Muli";
    font-style: normal;
    src: url('fonts/Muli-VariableFont_wght.ttf') format("truetype");
}
@font-face {
    font-family: "Muli";
    font-style: italic;
    src: url('fonts/Muli-Italic-VariableFont_wght.ttf') format("truetype");
}

body {
    font-family: "Muli";
    font-size: calc(4px + 2vmin);
    width: 100vw;
    overflow: overlay;
}

h1 {
    font-family: "Baskerville";
    font-size: calc(16px + 8vmin);
    font-weight: normal;
}
h2 {
    font-family: "Baskerville";
    font-size: calc(10px + 5vmin);
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
    font-weight: normal;
}
a{
    color: #666666;
}

.watercolor-page{
    background-color: white;
    overflow: hidden;
    position: relative;
    /*grid-template-columns: 1fr 5fr 1fr 2fr 1fr;
    grid-template-rows: 25% 100px auto;*/
}
.watercolor-header{
    /*background-color: rgba(255,192,203, .1);*/
    position: fixed;
    display: flex;
    align-items: center;
    /*height: calc(18px + 12vmin);*/
    width: 100%;
    mix-blend-mode: exclusion;
    z-index: 5;
}
.watercolor-section{
    width: 100%;
    display: flex;
    z-index: 1;
}
.watercolor-slides{
    flex: auto;
}
.watercolor-slide{
    width: 100%;
    height: calc(140vmin - 20vh);
    min-height: 25vh;
    max-height: 250vw;
    display: flex;
}
.watercolor-category{
    /*background-color: blue;*/
    flex: 0 0 calc(18px + 9vmin);
}
.watercolor-category > div{
    width: calc(18px + 9vmin);
    padding-top: calc(18px + 9vmin);
}
.watercolor-category > div.stuck{
    position: fixed;
    top: 0;
}
.watercolor-category > div > h2{
    color: #333333;
    margin: 0 auto;
    writing-mode: vertical-lr;
    text-orientation: mixed;
    transform: rotate(180deg);
}
.watercolor-sheet{
    flex: 0 0 45vw;
    background-image: url('images/watercolor-background-body.png');
    background-size: 100% auto;
    background-repeat: repeat-y;
    -webkit-filter: drop-shadow(0px 0px 12px rgba(0,0,0,0.4));
    filter:         drop-shadow(0px 0px 12px rgba(0,0,0,0.4));
    clip-path: polygon(-10% -10%, 0.5% -10%, 0.5% calc(0% + 1px),
                        99.5% calc(0% + 1px), 99.5% -10%, 110% -10%,
                        110% 110%, 99.5% 110%, 99.5% calc(100% - 1px),
                        0.5% calc(100% - 1px), 0.5% 110%, -10% 110% );
    margin-top: -3px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.watercolor-sidebar{
    flex: 1;
    margin-left: calc(18px + 9vmin);
    margin-right: calc(18px + 9vmin);
    display: flex;
    align-items: center;
}
.watercolor-sidebar h1{
    margin-block-start: 0;
    margin-block-end: calc(9px + 4.5vmin);;
}
.watercolor-sidebar > div{
    max-width: 650px;
}
.watercolor-image{
    width: 97%;
    mix-blend-mode: multiply;
    align-self: center;
}

.watercolor-leader{
    padding-top: calc(18px + 9vmin + 9px + 4.5vmin);
    width: 100%;
    display: flex;
    z-index: 0;
}
.watercolor-beginning{
    flex: 0 0 45vw;
    position: relative;
    padding-bottom: calc(45vw * 0.2286 + 10px + 5vmin);
}
.watercolor-beginning h1{
    margin-block-start: calc(10px + 5vmin);
    margin-block-end: calc(10px + 5vmin);
}
.watercolor-beginning *{
    position: relative;
    z-index: 2;
}
.watercolor-beginning .watercolor-sheet-tip{
    width: 100%;
    height: 100%;
    max-width: 45vw;
    position: absolute;
    bottom: -1px;
    z-index: 0;
    background-image: url('images/watercolor-background-tip.png');
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: bottom;
    background-position-x: left;
    -webkit-filter: drop-shadow(0px 0px 12px rgba(0,0,0,0.4));
    filter:         drop-shadow(0px 0px 12px rgba(0,0,0,0.4));
}
.watercolor-introduction{
    /*background-color: teal;*/
    flex: 1;
    margin-left: calc(18px + 9vmin);
    margin-right: calc(18px + 9vmin);
}
.watercolor-introduction p{
    margin-top: calc(32px + 16vmin);
}

.watercolor-follower{
    width: 100%;
    display: flex;
    z-index: 1;
}
.watercolor-ending{
    flex: 0 0 45vw;
    position: relative;
    padding-bottom: calc(18px + 9vmin);
}
.watercolor-ending .watercolor-sheet-tip{
    width: 100%;
    height: calc(45vw * 0.2286);
    padding-bottom: calc(9px + 4.5vmin);
    position: relative;
    margin-top: -2px;
    z-index: 1;
    background-image: url('images/watercolor-background-tip-flip.png');
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: top;
    -webkit-filter: drop-shadow(0px 0px 12px rgba(0,0,0,0.4));
    filter:         drop-shadow(0px 0px 12px rgba(0,0,0,0.4));
}
.watercolor-follower-link{
    display: table;
    margin: .3em 0;
}
.watercolor-conclusion{
    /*background-color: teal;*/
    flex: 1;
    margin-left: calc(18px + 9vmin);
    margin-right: calc(18px + 9vmin);
    margin-top: calc(45vw * 0.2286 + 22px + 11vmin);
}

.menu-logo{
    height: calc(6px + 3vmin);
    width: calc(6px + 3vmin);
    margin: calc(6px + 3vmin);
    display: inline-block;
    box-sizing: border-box;
    border: transparent solid calc(1px + 0.5vmin);
    transition-property: margin-left;
    transition-duration: .2s;
    transition-timing-function: cubic-bezier(1,-.2,0,1.2);
}
.menu-logo.scrolled{
    margin-left: calc(24px + 12vmin + 45vw);
}
.title-name{
    color: white;
}

.watercolor-pens{
    position: absolute;
    z-index: 0;
    top: calc(-16vw + 6vmin);
    right: -4vw;
    width: 64vw;
    -webkit-filter: drop-shadow(0px 0px 12px rgba(0,0,0,0.2));
    filter:         drop-shadow(0px 0px 12px rgba(0,0,0,0.2));
}

::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background: transparent;
}
::-webkit-scrollbar-thumb {
    background-image: url('images/watercolor-background-body.png');
    background-color: rgba( 64, 64, 64, .4);
    border-radius: 5px;
    /*transition: background-color 1s linear;*/
}
::-webkit-scrollbar-thumb:hover {
    background-color: rgba( 64, 64, 64, .6);
}
